.mobile-menu {
  width: 100%;
  height: 100%;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: #85a279;
  transition: left .75s cubic-bezier(.4, 0, .2, 1), visibility .75s cubic-bezier(.4, 0, .2, 1), opacity .75s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 100%;
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  left: 0;
}

.mobile-menu__container {
