.backdrop {
  width: 100%;
  height: 100%;
  z-index: 20000;
  background-color: rgba(0, 0, 0, .2);
  transition-property: opacity, visibility, scale;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  scale: 1;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  scale: 1.5;
}

.modal-input-fields {
  width: 280px;
  background: #fff;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  margin: 4% auto;
  padding: 28px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

@media screen and (min-width: 768px) {
